import React, { useEffect, useState } from "react";
import s from "./Slide.module.scss";
import { Link } from "react-router-dom";
import useAuth from "../providers/useAuth";
import ButtonBookmark from "../ButtonBookmark/ButtonBookmark";
import { LazyLoadImage } from "react-lazy-load-image-component";
import PlaceHolderImage from "../../images/placeholderImg.svg";
// import { useResize } from "../resize";

export default function Slide({ el, addFilms, isLoading }) {
  const { user } = useAuth();
  // const { width } = useResize();

  const [height, setHeight] = useState(0);
  useEffect(() => {
    if (window.innerWidth < 600) {
      setHeight(200);
    }
    if (window.innerWidth > 500) {
      setHeight(260);
    }
    if (window.innerWidth < 400) {
      setHeight(150);
    }
  }, [window.innerWidth]);
  console.log(isLoading);
  return (
    <>
      <Link to={"/movie/" + el.id} className={s.slide__link}>
        <span className={s.slide__rating}>{el.rating ? el.rating.kp.toFixed(1) : ""}</span>
        <div className={s.slide__img}>
          {isLoading ? (
            <img src="../../images/placeholderImg.svg"></img>
          ) : (
            <LazyLoadImage src={el.poster ? el.poster.previewUrl : ""} alt={el.name} placeholderSrc={PlaceHolderImage} height={height} />
          )}
        </div>
      </Link>
      <div className={s.slide__info}>
        <h2 className={s.slide__title}>{el.name}</h2>
        <div className={s.slide__text}>
          {el.movieLength ? `${el.year}, ${el.movieLength}мин` : el.year}
          {user && <ButtonBookmark el={el} addFilms={addFilms}></ButtonBookmark>}
        </div>
      </div>
    </>
  );
}
